.container {
  text-align: center;
  background-color: white;
  min-height: 100vh;
  font-size: 4rem;
  font-family: "Raleway", sans-serif;
  /* color: white; */
}

/* Search result table*/
.container table {
  margin-right: auto;
  margin-left: auto;
  /* border: 1px solid #3c4f7a; I think this looks bad*/

  text-align: center;
  border-collapse: collapse;
  width: auto;
}

.container table thead {
  background-color: #3c4f7a;
  padding: 12px;
  padding-left: 3rem;
  padding-right: 3rem;
  color: white;
}

.container table thead tr th {
  color: #c1efe2;
  font-size: 3rem;
}

.container table th,
td {
  color: black;
  padding: 12px;
  padding-left: 5rem;
  padding-right: 5rem;
  border-bottom: 1px solid #ddd;
  font-weight: normal;
}

.container table th td {
  color: white;
}

.container table tr td form input {
  height: 3rem;
  width: 10rem;
  font-size: 25px;
  border: 1px solid black;
  padding: 5px 5px 5px 60px;
  font-family: "Raleway", sans-serif;
}

/* End table stuff, Stats stuff below. */

/* Buttons */

.container button {
  border: none;
  font-size: 1rem;
  background-color: #009900;
  color: white;
  padding: 0.75rem;
}

.container #remove {
  border: none;
  font-size: 1rem;
  background-color: red;
  color: white;
  margin-left: 5px;
  padding: 0.75rem;
}

.container button:hover {
  cursor: pointer;
  opacity: 0.8;
}

/* headers */
.container h1 {
  font-size: 4rem;
}

.container h2 {
  margin-top: 0px;
  font-size: 6rem;
  color: #2a3755;
}

.container h3 {
  font-size: 2.5rem;
  margin-left: 7rem;
  margin-right: 7rem;
}

.container h3 #desiredTime {
  height: auto;
  width: auto;
  margin: 1rem;
  font-size: 2.5rem;
  padding: auto auto auto 4rem;
  border: 1px solid black;

  font-family: "Raleway", sans-serif;
}

.container h3 input {
  height: auto;
  width: 4rem;
  margin: 1rem;
  font-size: 2.5rem;
  border: 1px solid black;
  padding: auto auto auto 4rem;
  font-family: "Raleway", sans-serif;
}

.container h3 select {
  height: auto;
  width: auto;
  margin: 1rem;
  font-size: 2.5rem;
  border: 1px solid black;
  padding: auto auto auto 4rem;
  font-family: "Raleway", sans-serif;
}
